<template>
  <el-container direction="vertical">
    <el-divider content-position="left"><span class="title">商店</span></el-divider>
    <el-space wrap>
      <el-card class="box-card" v-for="(c,i) in this.$store.state.allExcSet" :key="i">
        <el-space class="title">{{c.name}}</el-space>
        <el-divider></el-divider>
          <el-space class="subtitle" style="margin-top:0px;">作者: {{c.author}}</el-space><br>
          <el-space class="subtitle">题数: {{c.total}}</el-space><br>
          <el-space class="subtitle">更新: {{c.update}}</el-space>          
        <el-space style="margin-top:30px">
          <el-button circle icon="el-icon-star-off" style="margin-right:20px" 
            type="primary" v-on:click="addExcSet(c)"></el-button>
        </el-space>
      </el-card>
    </el-space>
  </el-container>
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
    mounted: async function(){
    var data;
    await this.$axios.get("excset.json").then(res => {
      data=JSON.parse(res.request.responseText);//单纯的赋值无法得到
    })
    this.$store.commit('setAllExcSet',data)
  },
  components:{
    ElMessage,
  },
  methods:{
    addExcSet:function(c){
      for(var i in this.$store.state.myExcSet){
        if(this.$store.state.myExcSet[i].name==c.name){
          ElMessage.error({message:c.name+'已经在您的收藏中!'});
          return;
        }
      }
      this.$store.commit('addMyExcSet',c)
      ElMessage.success({message:c.name+'加入收藏!',type:'success'});
    }
  }
}
</script>

