<template>
<el-container direction="vertical">
  <el-divider content-position="left">
    <span class="title">{{this.$store.state.myExcSet[this.$store.state.curExcSet].name}}</span>
  </el-divider>
  <div :id="i" v-for="(d,i) in this.curExcList" :key="i">
    <el-card >
      <span>{{d.id}}&nbsp;&nbsp;{{d.text}}</span>
      <el-divider style="margin-bottom:0"></el-divider>
      <div v-bind:class="{choice:true,active:c.active}" v-for="(c,j) in d.choices" :key="j"
        v-on:click.left="clickLeft(c,d)" v-on:click.right="clickRight(c,d)">
        <span>{{c.id}}&nbsp;{{c.text}}</span>
      </div>
      <div v-bind:class="clacResultStyle(d)" v-if="d.submit">答案&nbsp;{{d.key.join(' ')}}</div>
      <div v-else v-bind:class="{choice:true}"><span>答案</span></div>
    </el-card>
  </div>
  <div class="bar">
    <div><el-button circle icon="el-icon-circle-check" type="primary" 
      style="margin:5px;" v-on:click="submitAll()"></el-button></div>
    <div><el-button circle icon="el-icon-refresh-right" type="primary" 
      style="margin:5px;" v-on:click="resetAll()"></el-button></div>
    <div><el-button circle icon="el-icon-thumb" type="primary" 
      style="margin:5px;" v-on:click="toCur()"></el-button></div>
    <div><el-button circle icon="el-icon-more" type="primary" 
      style="margin:5px;" v-on:click="this.hidden=!this.hidden"></el-button></div>
    <el-collapse-transition>
      <div v-show="!this.hidden">
      <div><el-progress style="margin-top:10px;" 
        type="circle" :percentage="Math.round(this.span%60/60*1000)/10" :width="100">{{this.stdSpan}}</el-progress></div>
      <div><el-progress style="margin-top:10px;" 
        type="circle" :percentage="this.correctRate" status="success" :width="100">
        {{this.correctRate}}%</el-progress></div>
      </div>
    </el-collapse-transition> 
  </div>
</el-container>      
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
  components:{
    ElMessage,
  },
  data:function(){
    return{
      curExcList:[],
      corList:[],
      doneList:[],
      curExc:0,
      span:0,
      hidden:true,
      timer: setInterval(() => {
        this.span+=1
      }, 1000)
    }
  },
  beforeUnmount(){
    if(this.timer){
      clearInterval(this.timer);
    }
    this.save()    
  },
  mounted:async function(){
    var curExcSet=this.$store.state.myExcSet[this.$store.state.curExcSet]
    var data = localStorage.getItem(curExcSet.name)
    var t = [];
    if(!data){
      await this.$axios.get(curExcSet.path).then(res => {
        var json = JSON.parse(res.request.responseText); 
        t.push.apply(t, json);
      })
      this.curExcList = t;
    }
    else{
      var json = JSON.parse(data);
      this.curExc = json.curExc;
      this.corList = json.corList;
      this.doneList = json.doneList;
      this.curExcList = json.curExcList;
    }
  },
  methods:{  
    save(){
      this.$store.commit('updateDone',this.doneList.length)
      var data = {}
      data.curExc = this.curExc;
      data.curExcList = this.curExcList;
      data.corList = this.corList;
      data.doneList = this.doneList;
      var curExcSet=this.$store.state.myExcSet[this.$store.state.curExcSet]
      localStorage.setItem(curExcSet.name, JSON.stringify(data));
    },
    toCur(){
      window.location.hash = "#"+String(this.curExc);
    },
    checkChooseLogic(exc, choice){
      if(exc.key.length == 1){
        for(var i = 0; i < exc.choices.length; i++){
          if(exc.choices[i].id != choice.id){
            exc.choices[i].active = false;
          }
        }
      }
    },
    handleSubmit(exc){
      this.$data.curExc = exc.id;
      this.$data.doneList.push(exc.id);      
      var i = 0;
      for(var j = 0; j < exc.choices.length; j++){
        //对于每一个选项，如果被选择，那么查看是否在答案中
        if(exc.choices[j].active){
          if(exc.key.indexOf(exc.choices[j].id) == -1){
            return;
          }
          i++;
        } 
      }
      if(i != exc.key.length){
          //多选了选项
          return;
      }
      this.$data.corList.push(exc.id);
    },
    handleReset(exc){
      var idx = this.doneList.indexOf(exc.id);
      if(idx != -1){
        this.doneList.splice(idx, 1);
      }
      var cidx = this.corList.indexOf(exc.id);
      if(cidx != -1){
        this.corList.splice(cidx, 1);
      }
    },
    clacResultStyle(exc){
      if(!exc.submit){
        return {
          'choice': true,
          'wrong': false,
          'correct': false,
        }
      }
      var i = 0;
      for(var j = 0; j < exc.choices.length; j++){
        //对于每一个选项，如果被选择，那么查看是否在答案中
        if(exc.choices[j].active){
          if(exc.key.indexOf(exc.choices[j].id) == -1){
            return {
              'choice': true,
              'wrong': true,
              'correct': false,
            }
          }
          i++;
        } 
      }
      if(i != exc.key.length){
        //多选了选项
        return {
          'choice': true,
          'wrong': true,
          'correct': false,
        }
      }
      return {
        'choice': true,
        'wrong': false,
        'correct': true,
      }
    },
    clickLeft(choice,exc){
      choice.active=exc.submit?choice.active:!choice.active; 
      if(!exc.submit){
        this.checkChooseLogic(exc, choice);
      }  
    },
    clickRight(choice,exc){
      if(exc.submit && choice.active){ 
        exc.submit = !exc.submit;
        this.handleReset(exc); 
      } 
      else if(!exc.submit && choice.active){
        exc.submit = !exc.submit; 
        this.handleSubmit(exc);
      } 
      else if(!exc.submit && !choice.active){
        exc.submit = !exc.submit; 
        choice.active = true; 
        this.checkChooseLogic(exc, choice); 
        this.handleSubmit(exc);
      }
    },
    submitAll(){
      for(var i in this.curExcList){
        var active = false;
        for(var j in this.curExcList[i].choices){
          if(this.curExcList[i].choices[j].active){
            active = true;
            break;
          }
        }
        if(active){
          this.curExcList[i].submit = true;
          this.handleSubmit(this.curExcList[i]);
        }
      }
      this.save()
      ElMessage.success({message:'提交成功!',type:'success'});
    },
    resetAll(){
      for(var i in this.curExcList){
        this.curExcList[i].submit = false;
        for(var j in this.curExcList[i].choices){
            this.curExcList[i].choices[j].active = false;
        }
        this.handleReset(this.curExcList[i]);
      }
      this.save();
      ElMessage.success({message:'重置成功!',type:'success'});
    },
  },
  computed:{
    correctRate(){
      if(!this.doneList.length){
        return 0;
      }
      return Math.round(this.corList.length/this.doneList.length*1000)/10
    },
    stdSpan(){
      return ("00"+Math.floor(this.span/360).toString()).slice(-2)+":"+("00"+Math.floor(this.span/60).toString()).slice(-2)+":"+("00"+(this.span%60).toString()).slice(-2)
    }
  }
}
</script>

<style>
.bar {
  position:fixed;
  margin-left:80%;
  text-align: center;
  width:200px;
}
.el-card {
  margin-top: 30px;
  padding-left: 0%;
}
.choice.active {
  color: #606266;
  background-color: #DCDFE6;
}
.choice.correct {
  color: white;
  background-color:#67C23A ;
}
.choice.wrong {
  color: #DCDFE6;
  background-color: #F56C6C;
}
.choice {
  padding: 10px;
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB',
  'Microsoft YaHei', '微软雅黑', Arial, sans-serif;
  border: 1px solid #DCDFE6;
  border-radius: 2px;
  margin-top: 10px;
}
</style>