<template>
  <el-container>
    <el-aside style="width:60px;position:fixed">
      <el-menu>
        <el-menu-item v-on:click="toUser()">
          <el-icon :size="20">
            <user-filled v-if="this.$store.state.curPage=='user'"/>
            <user v-else/>
          </el-icon>
        </el-menu-item>
        <el-menu-item v-on:click="toShop()">
          <el-icon :size="20">
            <goods-filled v-if="this.$store.state.curPage=='shop'" />
            <goods v-else/>
          </el-icon> 
        </el-menu-item>
        <el-menu-item v-on:click="toMore()">
          <el-icon :size="20">
            <more-filled v-if="this.$store.state.curPage=='more'" />
            <more v-else/>
          </el-icon>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main v-if="this.$store.state.curPage=='exc'">
      <exc-page></exc-page>
    </el-main>
    <el-main v-else-if="this.$store.state.curPage=='user'">
      <user-page></user-page>
    </el-main>
    <el-main v-else-if="this.$store.state.curPage=='shop'">
      <shop-page></shop-page>
    </el-main>
    <el-main v-else>
      <more-page></more-page>
    </el-main>
  </el-container>
</template>

<script>
import { User } from '@element-plus/icons'
import { Goods } from '@element-plus/icons'
import { More } from '@element-plus/icons'
import { UserFilled } from '@element-plus/icons'
import { GoodsFilled } from '@element-plus/icons'
import { MoreFilled } from '@element-plus/icons'
import ExcPage from './components/ExcPage.vue'
import MorePage from './components/MorePage.vue'
import ShopPage from './components/ShopPage.vue'
import UserPage from './components/UserPage.vue'
export default {
  components: {
    'exc-page':ExcPage,
    'more-page':MorePage,
    'user-page':UserPage,
    'shop-page':ShopPage,
    'user':User,
    'goods':Goods,
    'more':More,
    'user-filled':UserFilled,
    'goods-filled':GoodsFilled,
    'more-filled':MoreFilled
  },
  mounted:function(){
    //app中加载myExcSet(localStorage)和allExcSet(axios)
    var data = localStorage.getItem("myExcSet")
    if(!data){
      this.$store.commit('setMyExcSet',[])
    }
    else{
      var json = JSON.parse(data);
      this.$store.commit('setMyExcSet',json)
    }
    var that=this
    this.$axios.get('excset.json').then(res => {
      var json = JSON.parse(res.request.responseText); 
      that.$store.commit('setAllExcSet',json)      
    })
  },
  methods:{
    toUser:function(){
      this.$store.commit('setCurPage','user')
    },
    toMore:function(){
      this.$store.commit('setCurPage','more')
    },
    toShop:function(){
      this.$store.commit('setCurPage','shop')
    },
  }
}
</script>
  
