<template>
  <el-container direction="vertical">
    <el-divider content-position="left"><span class="title">您的收藏</span></el-divider>
    <el-empty v-if="this.$store.state.myExcSet.length==0" description="您的收藏为空"></el-empty>
    <el-space wrap>
      <el-card class="box-card" v-for="(c,i) in this.$store.state.myExcSet" :key="i">
        <el-progress type="circle" :percentage="pre(i)"></el-progress>
        <el-space class="title">{{c.name}}</el-space>
        <el-space style="margin-top:60px">
          <el-button circle icon="el-icon-edit"  type="primary" 
            v-on:click="toExc(i)"></el-button>
          <el-button circle icon="el-icon-delete" type="primary" 
            v-on:click="remove(i)"></el-button>
          <el-button circle icon="el-icon-refresh-right" type="primary" 
            ></el-button>            
        </el-space>
      </el-card>
    </el-space>
  </el-container>
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
  components:{
    ElMessage,
  },
  methods:{
    pre:function(i){
      return Math.round(this.$store.state.myExcSet[i].done/this.$store.state.myExcSet[i].total*1000)/10
    },
    toExc:function(i){
      this.$store.commit('setCurPage','exc')
      this.$store.commit("setCurExcSet",i)
    },
    remove:function(i){
      var name=this.$store.state.myExcSet[i].name
      this.$store.commit('removeMyExcSet',i);
      ElMessage.success({message:name+'已删除!',type:'success'});
    }
  },
}
</script>

