import { createStore } from 'vuex'

export const store = createStore({
  state:{
    curExcSet:0,
    curPage:'user',
    allExcSet:[], 
    myExcSet:[],
  },
  mutations:{
    setAllExcSet:function(state,all){
      state.allExcSet=all
    },
    setMyExcSet:function(state,my){
      state.myExcSet=my
    },
    addMyExcSet:function(state,c){
      state.myExcSet.push({name:c.name,author:c.author,done:0,total:c.total,update:c.update,path:c.path})
      localStorage.setItem('myExcSet',JSON.stringify(state.myExcSet))
    },
    removeMyExcSet:function(state,i){
      state.myExcSet.splice(i,1);
      localStorage.setItem('myExcSet',JSON.stringify(state.myExcSet))
    },
    setCurExcSet:function(state,i){ 
      state.curExcSet=i;
    },
    setCurPage:function(state,page){
      state.curPage=page;
    },
    updateDone:function(state,done){
      state.myExcSet[state.curExcSet].done=done;
      localStorage.setItem('myExcSet',JSON.stringify(state.myExcSet))
    }
  }
})
